
* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}
body,
html {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  height: 100%;
}
#app {
  color: #2c3e50;
  height: 100%;
}
footer {
  background: #00b0e6;
  padding: 40px 0;
  text-align: center;
  position: relative;
}
footer h2 {
  color: #fff;
  font-size: 25px;
}
footer p {
  color: #fff;
  font-size: 16px;
}
h1 {
  color: #77ae29;
  font-size: 30px;
  font-weight: 700;
}
h2 {
  color: #77ae29;
  font-size: 20px;
  font-weight: 700;
}
header {
  position: relative;
}
header .header-mutual {
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 20px 0;
}
.animated {
  -webkit-animation-duration: 1.8s;
          animation-duration: 1.8s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
.fadeInUp {
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
}
.has-scroll-reveal {
  opacity: 0;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.no-padding {
  padding: 0;
}
@-webkit-keyframes fadeInUp {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, 2.5rem, 0);
            transform: translate3d(0, 2.5rem, 0);
}
to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
}
}
@keyframes fadeInUp {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, 2.5rem, 0);
            transform: translate3d(0, 2.5rem, 0);
}
to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
}
}


